<template>
  <div class="mx-auto w-75 mt-5">
    <div class="display-4 text-white text-left">主題歌單</div>
    <div class="container-fluid mt-4">
      <div class="row">
        <div
          class="col-6 col-sm-3 mb-3"
          v-for="item in themeList"
          :key="item.id"
          >
          <a :href="item.url" target="_blank">
            <div class="card bg-dark text-white">
              <div class="position-relative">
                <img :src="item.images[1].url" class="card-img" alt="...">
                <div class="bg-gray"></div>
              </div>
              <div class="card-img-overlay otherTitle">
                <h5 class="card-title text-truncate">{{ item.title }}</h5>
              </div>
            </div>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      themeList: []
    }
  },
  created () {
    const vm = this
    const head = { headers: { Authorization: 'Bearer m2CYdp5FHaDLHWhYoaHSIg==' } }
    // 取得情境電台列表
    this.axios.get('https://api.kkbox.com/v1.1/featured-playlists?territory=TW', head)
      .then(function (res) {
        vm.themeList = res.data.data
      })
      .catch(error => console.error(error))
  }
}
</script>

<style lang="scss">
.col-6{
  h5{
    font-size: 5px;
  }
  overflow: hidden;
}
.bg-gray{
  background-color: rgba(128, 128, 128, 0.568);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  transition: 0.5s;
}
.bg-gray:hover{
  background-color: rgba(128, 128, 128, 0.377);
}
.card:hover .text-truncate{
    white-space: normal;
}
</style>
